.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


#slider-form {
  text-align: right;
  margin-top: 100px;
}

@media screen and (-webkit-min-device-pixel-ratio:0) {
  input[type='range'] {
    overflow: hidden;
    width: 225px;
    margin: 20px;
    -webkit-appearance: none;
    background-color: #d4d4d0;
  }

  input[type='range']::-webkit-slider-runnable-track {
    height: 12px;
    -webkit-appearance: none;
    color: #723ee7;
    margin-top: -1px;
  }

  input[type='range']::-webkit-slider-thumb {
    width: 10px;
    -webkit-appearance: none;
    height: 12px;
    cursor: ew-resize;
    background: #282727;
    box-shadow: -225px 0 0 225px #723ee7;
  }
}

#duration-slider {
  width: 400px;
}

#play-button {
  margin: 50px;
}

#bottom-div {
  float: bottom;
  /*background-color: green;*/
  width: 100%;
  margin-top: 100px;
}

#duration-output {
  text-align: left;
}

#tempo-output {
  text-align: right;
}

